<template>
    <el-dialog class="add-team-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm">
        <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="100px">
            <el-row :gutter="15">
              <el-col :span="8">
                <el-form-item label="网格名称" prop="name">
                    <el-input v-model="dialogForm.name" placeholder="请填写网格名称"></el-input>
                </el-form-item>
                <el-form-item label="性别" prop="sex">
                    <el-select placeholder="请选择性别" clearable v-model="dialogForm.sex">
                        <el-option v-for="(item, index) in dic['性别']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="出生日期" prop="birth">
                    <el-date-picker v-model="dialogForm.birth" type="date" placeholder="请选择日期"></el-date-picker>
                </el-form-item>  
                <el-form-item label="网格员数量" prop="gridpeoplrCount">
                    <el-input v-model="dialogForm.gridpeoplrCount" :maxlength="6" placeholder="请填写网格员数量"></el-input>
                </el-form-item>                
                <el-form-item label="固定电话" prop="telephoneNumber">
                    <el-input v-model="dialogForm.telephoneNumber" placeholder="请填写固定电话"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="网格长" prop="gridheadName">
                    <el-input v-model="dialogForm.gridheadName" placeholder="请填写网格长姓名"></el-input>
                </el-form-item>  
                <el-form-item label="民族" prop="nation">
                    <el-select placeholder="请选择名族" clearable v-model="dialogForm.nation">
                        <el-option v-for="(item, index) in dic['民族']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>  
                <el-form-item label="学历" prop="education">
                    <el-select placeholder="请选择机构" clearable v-model="dialogForm.education">
                        <el-option v-for="(item, index) in dic['学历']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="网格户数" prop="gridhouseCount">
                    <el-input v-model="dialogForm.gridhouseCount" :maxlength="6" placeholder="请填写网格户数"></el-input>
                </el-form-item> 
                <el-form-item label="网格编码">
                    <el-input v-model="dialogForm.code" :maxlength="20" placeholder="请填写网格编码"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="网格员" prop="gridpeopleName">
                    <el-input v-model="dialogForm.gridpeopleName" placeholder="请填写网格员姓名"></el-input>
                </el-form-item>  
                <el-form-item label="政治面貌" prop="politicsStatus">
                    <el-select placeholder="请选择政治面貌" clearable v-model="dialogForm.politicsStatus">
                        <el-option v-for="(item, index) in dic['政治面貌']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="手机号码" prop="phoneNumber">
                    <el-input v-model="dialogForm.phoneNumber" placeholder="请填写手机号码"></el-input>
                </el-form-item>  
                <el-form-item label="网格面积" prop="area">
                    <el-input v-model="dialogForm.area" :maxlength="8" placeholder="请填写网格面积(km²)"></el-input>
                </el-form-item> 
                <el-form-item label="责任区域" prop="responseArea">
                    <el-input v-model="dialogForm.responseArea" :maxlength="25" placeholder="请填写责任区域"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
			<el-button type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/zz_service.js";
export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      dic: {
        性别: [],
        民族: [],
        政治面貌: [],
        学历: []
      },
      dialogForm: {
        districtCode: "",
        sex: "1",
        nation: "01",
        politicsStatus: "13"
      },
      teamRules: {
        name: [
          { required: true, message: "请输入网格名称", trigger: "blur" },
          { min: 2, max: 25, message: "长度在 2 到 25 个字符", trigger: "blur" }
        ],
        gridheadName: [
          { required: true, message: "请输入网格长姓名", trigger: "blur" },
          { min: 2, max: 12, message: "长度在 2 到 12 个字符", trigger: "blur" }
        ],
        gridpeopleName: [
          { required: true, message: "请输入网格员姓名", trigger: "blur" },
          { min: 2, max: 12, message: "长度在 2 到 12 个字符", trigger: "blur" }
        ],
        sex: [{ message: "请选择性别", trigger: "change" }],
        nation: [{ message: "请选择民族", trigger: "change" }],
        politicsStatus: [{ message: "请选择政治面貌", trigger: "change" }],
        birth: [{ message: "请选择出生日期", trigger: "change" }],
        education: [{ message: "请选择学历", trigger: "change" }],
        phoneNumber: [
          { required: true, message: "请输入手机号码", trigger: "blur" },
          util.validate.valiTellPhone()
        ],
        telephoneNumber: [util.validate.tellPhone()],
        gridpeoplrCount: [
          { required: true, message: "请输入网格员数量", trigger: "blur" },
          util.validate.valiInteger()
        ],
        gridhouseCount: [
          { message: "请输入网格户数", trigger: "blur" },
          util.validate.valiInteger()
        ],
        responseArea: [
          {
            required: true,
            message: "请输入责任区域",
            trigger: "blur"
          }
        ]
      }
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },

  methods: {
    /** 保存 */
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let param = _.cloneDeep(this.dialogForm);
          param.birth = util.time.formatDate(param.birth, "YYYY-MM-DD");
          param.dicMap && delete param.dicMap;
          if (this.isAdd == true) {
            // 添加
            service.requestSaveGrid(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "综治网格添加成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          } else if (this.isAdd == false) {
            // 修改
            service.requestUpdateGrid(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "综治网格修改成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.dialogForm = {
        districtCode: "",
        sex: "1",
        nation: "01",
        politicsStatus: "13"
      };
    }
  }
};
</script>
<style>
.add-team-dialog .el-date-editor.el-input {
  width: 100%;
}
.add-team-dialog .el-select {
  width: 100%;
}
</style>